/* AboutUsSection.css */

.Aboutcontainer {
    position: relative;
    width: 100%;
    height: 666px;
    display: flex;
    align-items: center;
    background-color: #ffffffff;
    border-radius: 10px;
    overflow: hidden;
  }
  
  /* Background boxes */
  .backgroundBoxOne {
    position: absolute;
    top: 84px;
    left: 620px;
    width: 145px;
    height: 155px;
    background-color: #e0d6fc;
    border-radius: 10px;
    z-index: -2;
  }
  
  .backgroundBoxTwo {
    position: absolute;
    left: 51px;
    top: 441px;
    width: 182px;
    height: 161px;
    background-color: #fee4dd;
    border-radius: 10px;
    z-index: -2;
  }
  
  /* Main Content Section */
  .imageSection {
    flex: 1;
    display: flex;
    justify-content: center;
    z-index: 1;
  }
  
  .image {
    width: 541px;
    height: 395px;
    border-radius: 10px;
    object-fit: cover;
  }
  
  .textSection {
    flex: 1;
    padding: 20px;
    z-index: 2;
  }
  
  .subheading {
    color: #ff4e00;
    font-family: Quicksand;
    font-size: 20px;
    line-height: 30px;
    margin-bottom: 10px;
  }
  
  .heading {
    width: 641px;
    position: relative;
    color: #003e90;
    font-family: Quicksand;
    font-size: 60px;
    font-weight: 700;
    line-height: 60px;
  }
  
  /* .underline {
    position: absolute;
    width: 239px;
    height: 12px;
    top: 300px;
    left: 1105px;
    border-radius: 45px;
    z-index: -1;
  } */
  .underline {
    display: block; /* Ensure it behaves like a block element */
    margin: 0 auto; /* Center the image horizontally */
    width: 0; /* Start with a width of 0 for animation */
    height: auto; /* Maintain aspect ratio */
    position: absolute; 
    top: 300px; /* Adjust top position as needed */
    left: 80%; /* Center the image */
    transform: translateX(-50%); /* Adjust for centering */
    z-index: -1; /* Keep the image behind other content */
    animation: zigzagUnderline 6s ease-out infinite; /* Infinite animation */
}

@keyframes zigzagUnderline {
    0% {
        width: 0; /* Start with width 0 */
    }
    50% {
        width: 239px; /* Reach max width at 50% */
    }
    100% {
        width: 0; /* End with width 0 */
    }
}

  .call {
    font-family: Quicksand;
    font-size: 14px;
    line-height: 28px;
    font-weight: 600;
    color: #313131;
    margin: 0;
  }
  
  .features {
    width: 90%;
    display: grid;
    grid-template-columns: 1fr 1fr; /* Two columns */
    gap: 10px;
    margin-top: 20px;
    margin-bottom: 20px;
    z-index: 2;
}

.featureItem {
    width: 100%; /* Fill the entire grid cell */
    height: 48px;
    background: #ffffff00;
    padding: 10px;
    border-radius: 30px;
    box-shadow: 0px 4px 25px 0px #0000000f;
    display: flex;
    align-items: center; /* Center vertically */
    justify-content: flex-start; /* Align left */
    font-size: 17px;
    font-weight: 600;
    font-family: Quicksand;
    color: #313131;
}

  
  .checkMark {
    color: #313131;
    font-size: 22px;
    margin-right: 10px;
  }
  
  .button {
    width: 201.38px;
    height: 61px;
    border-radius: 22px;
    background-color: #ff4e00;
    font-family: Quicksand;
    color: #ffffff;
    font-size: 18px;
    line-height: 16px;
    border: none;
    cursor: pointer;
  }
  
  .contactInfo {
    display: flex;
    align-items: center;
  }
  
  .phoneIcon {
    font-size: 24px;
    color: #ff6b6b;
    margin-right: 10px;
  }
  
  .phoneNumber {
    font-family: Quicksand;
    font-size: 18px;
    line-height: 21.6px;
    font-weight: 600;
    color: #313131;
  }
  
  /* Clip art styles */
  .carClipArt {
    position: absolute;
    top: 20px;
    left: 20px;
    width: 91px;
    height: auto;
    opacity: 1;
  }
  .phone-link {
    color: inherit; /* Make link color inherit from parent */
    text-decoration: none; /* Remove underline from link */
}

.phone-link:hover {
    text-decoration: underline; /* Add underline on hover */
}
  .globeClipArt {
    position: absolute;
    bottom: 20px;
    right: 20px;
    width: 121px;
    height: auto;
    opacity: 1;
  }
  
  /* Responsive Styles */
  .actionSection {
    display: flex;
    align-items: center;
    gap: 50px;
  }
  
